import {NavLink} from 'react-router-dom'
import { connect } from 'react-redux'
import { signOut } from '../../store/actions/authActions'


const SignedInLinks=(props)=>{
    return(
       <ul className='right'>
           
           <li><NavLink style={{backgroundColor:'gray'}} to='/Dashboard'>Dashboard</NavLink></li>
           <li><NavLink style={{backgroundColor:'gray'}} to='/CreateProject'>New Project</NavLink></li>
           <li><a style={{backgroundColor:'gray'}} onClick={props.signOut}>LogOut</a></li>
           <li><NavLink style={{backgroundColor:'gray'}} to='/' className='btn btn-floating pink lighten-1'>{props.profile.initials}</NavLink></li>
       </ul>
    )
}

const mapDispatchToProps=(dispatch)=>{
    return {
        signOut : ()=>dispatch(signOut())        
         
    }
}

export default connect(null, mapDispatchToProps )(SignedInLinks) 