import {NavLink} from 'react-router-dom'


const SignedOutLinks=()=>{
    return(
       <ul className='right'>
           <li><NavLink style={{backgroundColor:'gray'}} to='/SignUp'>SignUp</NavLink></li>
           <li><NavLink style={{backgroundColor:'gray'}} to='/SignIn'>Login</NavLink></li>
       </ul>
    )
    
       
}

export default SignedOutLinks